import { Modelable } from '@globals/interfaces';
import { Component, EventEmitter, Element, Prop, State, Event, h, Listen } from '@stencil/core';
import { getValueByPath } from '@utils';

@Component({
  tag: 'hdc-select',
  styleUrl: 'hdc-select.scss',
})
export class HdcSelect implements Modelable {
  /**
   * Element of hdc select
   */
  @Element() el: HTMLElement;
  /**
   * Prop value of hdc select
   */
  @Prop({ mutable: true }) value: string | number | object | null;
  /**
   * Prop disable of hdc select
   */
  @Prop({ mutable: true }) disable: boolean;
  /**
   * Prop lock of hdc select
   */
  @Prop({ mutable: true }) lock: boolean;
  /**
   * Prop items of hdc select
   */
  @Prop({ mutable: true }) items: Array<string | object> | undefined;
  /**
   * Prop view items of hdc select
   */
  @Prop({ mutable: true }) viewItems: number = 8;
  /**
   * Prop placeholder of hdc select
   */
  @Prop() placeholder: string = '-- Seleccione --';
  /**
   * Prop key of hdc select
   */
  @Prop() valueKey = 'id';
  /**
   * Prop string of hdc select
   */
  @Prop() valueLabel = 'name';
  /**
   * State filtered items of hdc select
   */
  @State() filteredItems: Array<string | object> = [];
  /**
   * State search text of hdc select
   */
  @State() searchText = '';
  /**
   * State search text of hdc select
   */
  @State() showDrop: boolean = false;

  @Event() changeBlur: EventEmitter;
  @Event() changeValue: EventEmitter;
  /**
     * Max height of hdc select
     */
  maxHeight = 20;

  /**
   * Determines whether change value filter options on
   * @param event CustomEvent
   */
  onChangeValueFilterOptions(event: string) {
    this.searchText = event;
    this.filteredItems =
      this.items?.filter((option) => {
        const label =
          this.valueLabel && typeof option === 'object' ? option[this.valueLabel] : option;

        return label.toLowerCase().includes(this.searchText.toLowerCase());
      }) ?? [];
  }

  /**
   * Determines whether click select option on
   * @param option string
   */
  onClickSelectOption(option: string | object | null) {
    this.value = this.valueKey && option && typeof option === 'object' ? option[this.valueKey] : option;
    this.filteredItems = [];
    this.toggleDrop();
    this.clearInputSearch();
    this.changeValue.emit(this.value);
    this.el.blur();
  }

  @Listen('click', { target: 'window' })
  handleOnClick(ev){
    if(this.showDrop) {
      if (this.el.contains(ev.target)) {
        // If click was inside, stop here
        return;
      }
      this.showDrop = false;
      this.clearInputSearch();
      this.changeBlur.emit(ev);
    }
  }
  
  render() {
    return (
      <div class="hdc-select">
        <div 
          class={this.getClassSelectValue()}
          onClick={() => this.toggleDrop()}>
          <span class="hdc-select-value-text">{this.getValueKey()}</span>
          <span class="hdc-select-value-icon">
            <svg width="25" height="15" viewBox="0 0 392 448" xmlns="http://www.w3.org/2000/svg">
              <path fill="currentColor" d="m192 284l186-162q15-17 2-30q-17-17-30-2L192 228L36 90Q23 75 6 92q-14 14 3 30z"/>
            </svg>
          </span>
        </div>
        <div
          class={this.getClassInputAutocompleteContainer()}
          style={{ maxHeight: this.getMaxHeight() }}
        >
          <div class="hdc-select-search">
            <input 
              class="hdc-select-search-input"
              onInput={ev => this.onInputText(ev)}
            />
          </div>
          <ul class={this.getClassInputAutocomplete()}>
            <li
              class="hdc-select-item"
              onClick={() => this.onClickSelectOption(null)}
              >
              {this.placeholder}
            </li>
            {this.filteredItems.map((option, index) => (
              <li
                key={index}
                class="hdc-select-item"
                onClick={() => this.onClickSelectOption(option)}
              >
                {this.valueLabel && typeof option === 'object' ? option[this.valueLabel] : option}
              </li>
            ))}
          </ul>
        </div>
      </div>
    );
  }

  toggleDrop() {
    if(!this.showDrop) {
      this.onChangeValueFilterOptions('');
    }
    this.showDrop = !this.showDrop;
  }

  /**
   * Determines whether input text on
   * @param input Event
   */
  onInputText(input: InputEvent) {
    const element = input.target as HTMLInputElement;

    this.onChangeValueFilterOptions(element.value);
  }

  clearInputSearch() {
    const element = this.el.querySelector('input.hdc-select-search-input') as HTMLInputElement;
    if(element){
      element.value = '';
    }
  }

  /**
   * Gets max height
   * @returns { [key: string]: boolean }
   */
  getMaxHeight() {
    return this.viewItems > 0 ? `${(this.maxHeight * this.viewItems) + 40}px` : '';
  }

  /**
   * Gets class select container
   * @returns { [key: string]: boolean }
   */
  getClassInputAutocompleteContainer() {
    return {
      'hdc-select-container': true,
      'hdc-select-container--top': true,
      'show': this.showDrop,
    };
  }

  /**
   * Gets class select
   * @returns { [key: string]: boolean }
   */
  getClassInputAutocomplete() {
    return {
      'hdc-select-options': true,
    };
  }

  /**
   * Gets class select
   * @returns { [key: string]: boolean }
   */
  getClassSelectValue() {
    return {
      'hdc-select-value': true,
      'disabled': this.disable || this.lock,
      'show': this.showDrop,
    };
  }

  /**
   * Gets value key
   * @returns string
   */
  getValueKey() {
    if (this.value) {
      const itemSelected = this.items?.find((item: string | number | object) => {
        return typeof item === 'object' ? item[this.valueKey] == this.value : item == this.value;
      });
      return (
        this.valueKey && typeof itemSelected === 'object'
          ? getValueByPath(itemSelected, this.valueLabel.split('.'))
          : itemSelected
      ) as string;
    } else {
      return this.placeholder;
    }
  }

}
